      file_7: "", //运营文件
    };
  },
  methods: {
    getDefaultData() {
      //获取默认数据
      // console.log(this.container)
      axios({
        url: this._url2 + "server/stock_management/schedule_detail/",
        method: "get",
        params: {
          container_num: this.container,
        },
      }).then((res) => {
        console.log(res);
        // data_query_data  资料上传，报清关的相关数据
        // shipping_record_data 订舱配仓信息
        // 订舱
        let allcircle = document.querySelectorAll(".circle");
        allcircle[0].style.background = "greenyellow";
        // console.log(allcircle)
        var shipping_record_data = res.data.shipping_record_data;
        var data_query_data = res.data.data_query_data;
        var expect_container = res.data.expect_container;
        var port_receiving_data = res.data.port_receiving_data;

        if (
          shipping_record_data.destination_port &&
          shipping_record_data.boat_man &&
          shipping_record_data.box_type
        ) {
          // console.log('哈哈哈哈')
          // document.getElementById('booking_ship').style.background = '#13ce66';

          allcircle[2].style.background = "greenyellow";
        }
        // 配仓
        if (
          shipping_record_data.allocation_time &&
          shipping_record_data.box_type &&
          shipping_record_data.ship_name &&
          shipping_record_data.etd &&
          shipping_record_data.flight_name
        ) {
          // console.log('哈哈哈哈哈222')
          allcircle[3].style.background = "greenyellow";
          // 截单
        }
        if (shipping_record_data.cut_off_time) {
          // console.log('哈哈哈哈哈3333')
          allcircle[4].style.background = "greenyellow";
        }
        // 预计装柜

        if (expect_container.date) {
          allcircle[5].style.background = "greenyellow";
        }
        this.data_query_data = data_query_data;
        if (data_query_data.customs_clearance) {
          this.file_1 = data_query_data.customs_clearance.split(",");
        }
        if (data_query_data.cut_order) {
          this.file_2 = data_query_data.cut_order.split(",");
        }
        if (data_query_data.declare_customs) {
          this.file_3 = data_query_data.declare_customs.split(",");
        }

        // 资料上传
        if (
          data_query_data.customs_clearance &&
          data_query_data.declare_customs &&
          data_query_data.cut_order
        ) {
          allcircle[6].style.background = "greenyellow";
        }

        if (
          port_receiving_data.receive_count != 0 &&
          port_receiving_data.receive_count
        ) {
          allcircle[7].style.background = "greenyellow";
          allcircle[8].style.background = "greenyellow";
        }
        // let flag = 0;
        // if (res.data.operating_data_data[0].calculation_file_path) {
        //   flag++;
        // }
        // for (let i = 0; i < res.data.operating_data_data.length; i++) {
        //   if (res.data.operating_data_data[i].bar_code) {
        //     flag++;
        //     break;
        //   }
        // }
